
.nav {
	height: 50px;
	width: 100%;
	font-size: 14px;
	background-color: #FAFAFA;
}
.nav li {
	height: 50px;
	width: 50px;
	line-height: 50px;
	display: inline-block;
	text-align: center;
	color: #292E35;
	cursor: pointer;
	-webkit-user-select: none;  /*谷歌禁止选择*/
}
.nav li:hover {
	color: red;
}



